<div class="faq"><section><h1 class="ui center aligned header">自學問答 </h1><h4 class="ui center aligned header">分類：<div class="ui small buttons"><button class="ui button" v-for="c in catagories" :style="buttonStyle(c.t)" @click="myC = c.t" :title="c.description">{{c.t}}</button></div><!-- button.ui.button(@click="myC = undefined") 全部--></h4></section><div class="ui divider"></div><br><div class="ui four column doubling stackable relaxed grid container" id="main"><div class="column" v-for="(f, index) in faqs" v-show="(!myC || f.c == myC) &amp;&amp; (!myKey || f.q.indexOf(myKey)&gt;-1)"><div class="inner bordered bottom right left"><h4 class="ui center aligned icon header"><router-link @click="myF=f" :to="'/ans/' + index"><i class="circular icon" v-bind:class="cataIcon(f.c)"></i><div class="description" v-html="highlightAndMakeBr(f.q, myKey)" v-bind:class="{orange: index % 2 == 0}"></div><br><div class="small gray note">{{ f.as[0] }}...</div></router-link></h4></div><a class="ui top left attached ribbon label" v-bind:style="{ 'background-color': cataColor(f.c) }" @click="myC = (!myC &amp;&amp; f.c) || 0">{{f.c}}</a></div></div></div>