<div class="ui segment container"><h1 class="ui header">第一次申請高中自學就上手</h1><p>原始來源：自主學習促進會 －2014年3.4.5月公益工作坊－文字記錄分享</p><p>內容更新與重編：2024年10月</p><p>編輯註記：因為原本的文字記錄中的超連結大多已經失效，所以重新整理並更新連結。</p><h2 class="ui header">為什麼要自學？</h2><ul class="ui list"><li>有時間做你想做的事，把它做到夠好，成為未來立足的專業</li></ul><h2 class="ui header">第一講：權利與程序</h2><ul class="ui list"><li>內容：法規講解，了解基本的自學權益和申請流程，進行計畫書第一部份理念的撰寫。</li><li>講座前，先將相關法規等資訊傳給參與者。</li></ul><p>(1) 請到以下<a href="https://law.moj.gov.tw/LawClass/LawAll.aspx?pcode=H0070059">全國法規資料庫</a>查閱《高級中等以下教育階段非學校型態實驗教育實施條例》，並且到你戶籍地的教育局網站下載計劃書和申請書的範例。</p><p>(2) 108新課程綱要，這是你在寫自學計劃書前一定要讀過的：<a href="https://shs.k12ea.gov.tw/site/12basic">點擊這裡</a></p><h3 class="ui header">高中不是義務教育，申請自學和直接不去上學，差別在哪？</h3><ul class="ui list"><li>申請高中自學，進行順利後可在應屆接領到完成證明，取得入學大學的同等學力。</li><li>申請自學會有學生證，可買學生票並享有高中職學生一樣的權利，參加各種競賽。</li><li>自學經驗可作為未來職場的履歷、簡報、面試準備。</li></ul><h3 class="ui header">審議會</h3><ul class="ui list"><li>教育行政代表、專長學者代表、教長教師代表、非學校家長代表、自學相關非營利社團代表。</li><li>代表不參與自己子女的審議，以確保公平。</li><li>重要審議重點：學生受教權是否得到保障，自學計劃的合理性與可行性，尊重多元智能。</li></ul><h3 class="ui header">計畫撰寫要點</h3><ul class="ui list"><li>核心理念、未來方向與目標。</li><li>計畫對象：需詳細描述學生本人的情況。</li><li>預期成效：具體寫出學生的目標與方向，反映個人的興趣。</li><li>師資：包括相關知識與經驗的人員。</li><li>課程內容：分為專精科目與一般科目，學分分配等。</li></ul><h2 class="ui header">第二講：如何開拓學習資源</h2><p>學習資源的分享、免費與付費資源、如何把學習資源寫入計畫中。</p><p><a href="https://www.slideshare.net/slideshow/ss-33460035/33460035">第二講簡報連結</a></p><p>一萬小時定律：任何一門深入的專業，大都需要在你有興趣的領域投入一萬小時。</p><h3 class="ui header">認識一萬小時定律</h3><p>一萬小時定律：任何一門深入的專業，大都需要在你有興趣的領域投入一萬小時。</p><p>一萬小時是多久呢？</p><ul class="ui list"><li>如果每週投入6小時，需32年達到專精。</li><li>如果每週投入30小時，6年即可達到領域專精。</li></ul><p>一萬小時定律的啟示：</p><ul class="ui list"><li>找到自己的興趣，持續投入時間。</li><li>不要害怕花時間在無聊的事情上，從中觀察興趣。</li></ul><h3 class="ui header">T與H：單一專長 vs. 兩種專長合併的思維</h3><ul class="ui list"><li>T：單一專長，深入某一領域，廣泛涉獵其他領域。</li><li>H：兩種專長合併，跨領域發展，創新組合。</li></ul><p>T與H的例子：</p><ul class="ui list"><li>T：程式設計師，先深入程式設計領域，廣泛涉獵其他領域。</li><li>H：心理學家與程式設計師的組合，創新組合。</li></ul><h3 class="ui header">如果還不知道自己的興趣？</h3><ul class="ui list"><li>分辨興趣 vs 打發時間的活動，讓自己無聊但不做無意義的事，從中觀察興趣。</li><li>以「想做什麼」為核心，而不是「要當什麼」。</li></ul><h3 class="ui header">第三講：計畫書與審議</h3><ul class="ui list"><li>計畫書需包括完整的目標、方法、成效預期等。</li></ul><p><a href="https://www.slideshare.net/slideshow/ss-41035043/41035043">第三講簡報連結</a></p><p>關於自主學習促進會：<a href="https://www.alearn.org.tw/">自主學習促進會官網</a></p></div>